export function addComma(str) {
    if (str === void 0) { str = ''; }
    var result = [];
    str
        .split('')
        .reverse()
        .forEach(function (digit, index) {
        result.push(digit);
        if ((index + 1) % 3 === 0) {
            result.push(',');
        }
    });
    return result.reverse().join('');
}
