import { atom, selector } from 'recoil';
import { stringToNumber } from '../lib/stringToNumber';
import { addComma } from '../lib/addComma';
export var textState = atom({
    key: 'textState',
    default: '',
});
export var commaState = atom({
    key: 'commaState',
    default: true,
});
export var resultState = selector({
    key: 'resultState',
    get: function (_a) {
        var get = _a.get;
        var text = get(textState);
        var comma = get(commaState);
        var result = "" + stringToNumber(text);
        return comma ? addComma(result) : result;
    },
});
