import { charactorToNumber } from './charactorToNumber';
export function stringToNumber(str) {
    var result = 0;
    var multiplier = 1;
    var largeMultiplier = 1;
    var register = 0;
    var lastNumber = 0;
    for (var i = str.length - 1; i > -1; i--) {
        var current = charactorToNumber(str[i]);
        if (current !== null) {
            if (current === 0) {
                // ignore
            }
            else if (current >= 10000) {
                register += lastNumber * multiplier;
                lastNumber = 0;
                multiplier = 1;
                // is a large multiplier
                result += register * largeMultiplier;
                register = 0;
                largeMultiplier = current;
            }
            else if (current >= 10) {
                // is a multiplier
                register += lastNumber * multiplier;
                lastNumber = 0;
                multiplier = current;
            }
            else {
                lastNumber = current;
            }
        }
    }
    if (multiplier === 10 && lastNumber === 0) {
        register += multiplier;
    }
    else {
        register += lastNumber * multiplier;
    }
    result += register * largeMultiplier;
    return result;
}
